.card{
	overflow: hidden;
	display: grid;
	grid-template-rows: 20% 60% 20%;
	background-color: #4a505a;
/*	border-color: #7b6c40;
*/	margin-bottom: 10px;
	min-width: 434px;
	height: 70vh;
	width: 100vw;
}
.headOfCard{
	display: flex;
	width: 100vw;
	align-items: center;
	color: white;
	background-color: #4a505a;
	padding-top: 0px;
	margin-top: 0px;
	grid-column-start: 0;
	grid-column-end: 1;

}

.card > p{
}
h3{
	padding: 0px;
	margin: 0px;
}
.contentOfCard{
	width: 100vw;
	grid-row-start: 4;
	grid-row-end: 5;
}
.video{
	width: 100vw;
	max-width: 560px;
	grid-row-start: 2;
	grid-row-end: 3;
	align-items: center;
}
iframe{
	width: 100vw;
/*	padding: 1px;
*/	min-width: 200px;
	margin-top: 10px;
}